import { Icon } from '@iconify/react';
import { displayEventTime } from '../../utils/formatTime';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function EventTypeSelectedCard({
    eventType,
    handleSelectType,
    handleCancelType,
    defaultChecked = false
}) {
    return (
        <GradientBorderWrapper
            className={`px-2 md:px-6 p-4  justify-between bg-base-200 rounded-xl ksdEjn event-item${defaultChecked ? ` event-active-bg` : ``
                }`}
        >
            <div className='flex' >
                <div className="flex gap-2 h-full items-center w-full" onClick={handleSelectType}>
                    {/* <input
                    type="radio"
                    name="event-type-radio"
                    className="radio radio-primary radio-sm"
                    onChange={handleSelectedType}
                    defaultChecked={defaultChecked}
                /> */}
                    <div className="flex items-stretch justify-between flex-col">
                        <label className="text-xl">{eventType.name}</label>
                        <div>
                            <span className="text-stone-400">{displayEventTime({ ...eventType, containsTime: true })}</span>
                        </div>
                    </div>
                </div>

                {defaultChecked && (
                    <div className="flex flex-col justify-center">
                        <button className="btn  btn-circle btn-ghost btn-sm" onClick={handleCancelType}>
                            <Icon icon="iconoir:cancel" className="text-[#E9ECF4]" width={20} />
                        </button>
                    </div>
                )}
            </div>

            {/* <div className="flex flex-col justify-center">
                <div className="rounded-full items-center flex gap-1 px-3 py-1 bg-[#5A5A5A] text-yellow-400">
                    <span>{fNumber(eventType?.price)}</span>
                </div>
            </div> */}
        </GradientBorderWrapper>
    );
}
