import { useEffect, useState } from 'react';
import { toast } from 'react-toastify';

import { API_TICKET } from '../../config';

import axios from '../../utils/axios';

import { useSelector } from '../../redux/store';

import useAuth from '../../hooks/useAuth';
import useLocales from '../../hooks/useLocales';

import EventSelectedCard from '../../components/cards/EventSelectedCard';
import EventTypeSelectedCard from '../../components/cards/EventTypeSelectedCard';
import EventSearchComponent from '../../components/form/EventSearchComponent';

export default function EventChoose({ setStep, mode = 'sell', handleChangeEvent = () => { } }) {
    const { user } = useAuth();
    const { saved } = useSelector((state) => state.ticket);
    const { t } = useLocales();

    const [selectedEvent, setSelectedEvent] = useState(null);
    const [disabledForward, setDisabledForward] = useState(true);
    const [loading, setLoading] = useState(false);

    const onSelected = (evt) => {
        setSelectedEvent(evt);
    };
    const onDiscardEvent = () => {
        if (selectedEvent) {
            setSelectedEvent(null);
            if (mode === 'buy') {
                setDisabledForward(true);
            }
        }
    };

    const onSelectedType = (eventTypeId, dateTime, type) => {
        if (type === 'select') setSelectedEvent({ ...selectedEvent, eventTypeId, selectedDate: dateTime });
        else setSelectedEvent({ ...selectedEvent, eventTypeId: '', selectedDate: '' });
        setDisabledForward(type !== 'select' ? true : false);
    };

    const onNext = () => {
        setLoading(true);
        if (mode === 'sell') {
            axios
                .post(API_TICKET.saveTicketEvent, {
                    eventId: selectedEvent?._id,
                    typeId: selectedEvent?.eventTypeId
                })
                .then(({ status }) => {
                    if (status === 200) {
                        setDisabledForward(true);
                        setStep(1);
                    }
                })
                .catch(({ message }) => {
                    toast.error(message.toString());
                })
                .finally(() => {
                    setLoading(false);
                });
        }
        if (mode === 'buy') {
            handleChangeEvent(selectedEvent);
        }
    };

    useEffect(() => {
        if (saved && saved !== null && mode === 'sell') {
            const event = saved?.event;
            setSelectedEvent({
                ...event,
                eventTypeId: saved?.eventTypeId,
                selectedDate: saved?.dateTime
            });
            setDisabledForward(false);
        }
    }, [saved, mode]);

    useEffect(() => {
        if (selectedEvent && mode === 'buy') setDisabledForward(false);
    }, [selectedEvent, mode]);

    return (
        <div className="flex flex-col gap-4 justify-center items-start px-2">
            <div className="flex flex-col gap-4 mb-12 w-full">
                <h3 className="text-4xl md:text-[64px]">{t('title.select_event')}</h3>
                <div className='w-full md:w-1/2'>
                    {mode === 'sell' && (
                        <p className="text-stone-400">{t('description.select_event', { fullName: user?.fullName })}</p>
                    )}
                    {mode === 'buy' && (
                        <p className="text-stone-400">
                            {t('description.select_event_buy', { fullName: user?.fullName })}
                        </p>
                    )}
                </div>
            </div>
            {!selectedEvent && (
                <div className="w-full md:w-1/2 flex flex-col gap-4">
                    <EventSearchComponent handleSelected={(evt) => onSelected(evt)} />
                </div>
            )}

            {selectedEvent && (
                <div className="flex flex-col gap-8 w-full grid grid-cols-1 md:grid-cols-2 mb-12">
                    <EventSelectedCard event={selectedEvent} handleDiscardEvent={onDiscardEvent} />
                </div>
            )}

            {selectedEvent && mode === 'sell' && (
                <div className="w-full">
                    <div className="grid md:grid-cols-2 mb-8">
                        <div className="flex flex-col gap-2 ">
                            <h3 className="text-[28px]">{t('title.select_event_type')}</h3>
                            <p className="text-stone-400">{t('description.select_event_type')}</p>
                        </div>
                    </div>
                    <div className="grid md:grid-cols-2 gap-8">
                        {selectedEvent?.types?.map((type, index) => (
                            <EventTypeSelectedCard
                                key={index}
                                defaultChecked={type?.typeId === selectedEvent?.eventTypeId}
                                eventType={type}
                                handleSelectType={() => onSelectedType(type.typeId, type?.dateTime, 'select')}
                                handleCancelType={() => onSelectedType(type.typeId, type?.dateTime, 'cancel')}
                            />
                        ))}
                    </div>
                </div>
            )}

            {selectedEvent && (
                <div className="flex flex-col gap-8 justify-center items-end w-full">
                    <div className="max-w-2xl w-full mb-8">
                        <div className="h-6" />
                        <div className="w-full flex justify-end">
                            <button
                                disabled={disabledForward}
                                className={`btn btn-primary px-4 md:px-8 capitalize py-2 md:py-4 rounded-2xl text-base md:text-[19px] h-auto ${loading && 'loading'}`}
                                onClick={onNext}
                            >
                                {t('action.continue')}
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}
